<template>
  <div>
    <CustomHalfClipped>
      <template #content>
        <div
          v-for="(row, rowId) in section.rows"
          :key="row.id"
          class="work-details-sections__container row m-0"
        >
          <div
            v-for="column in row.columns"
            :key="column.index"
            :style="{ width: getWidth(column.ratio) }"
          >
            <div
              v-if="column.type === WORK_COLUMNS_TYPES.TITLE && column.fillable"
              class="work-details-sections__title"
            >
              {{ column.content }}
            </div>

            <div
              v-else-if="
                column.type === WORK_COLUMNS_TYPES.IMAGE && column.fillable
              "
              v-for="(img, i) in column.img_content"
              :key="img.id"
              @click="openImageModal(rowId, i)"
              style="cursor:pointer"
            >
              <img
                :src="img.url"
                width="100%"
                draggable="false"
                class="image-placeholder"
              />
            </div>

            <div
              v-else-if="
                column.type === WORK_COLUMNS_TYPES.VIDEO && column.fillable
              "
              v-for="video in column.vid_content"
              :key="video.id"
            >
              <video
                preload="auto"
                :id="`video${video.id}`"
                :src="video.url"
                width="100%"
                :style="{ cursor: video.is_auto_play ? 'pointer' : 'unset' }"
                :controls="!video.is_auto_play"
                :muted="video.is_auto_play"
                :loop="video.is_auto_play"
                :autoplay="video.is_auto_play"
                @click="e => toggleFullScreenMode(e, video)"
              ></video>
            </div>

            <div
              v-else-if="
                column.type === WORK_COLUMNS_TYPES.SLIDER && column.fillable
              "
            >
              <Carousel3d
                :autoplay="true"
                :autoplay-timeout="3000"
                :autoplayHoverPause="true"
                :height="400"
                :width="500"
                :border="0"
              >
                <Slide
                  v-for="(img, i) in column.img_content"
                  :index="i"
                  :key="i"
                >
                  <img
                    :src="img.url"
                    draggable="false"
                    class="image-placeholder"
                  />
                </Slide>
              </Carousel3d>
            </div>

            <div
              v-if="
                column.type === WORK_COLUMNS_TYPES.DESCRIPTION &&
                  column.fillable
              "
              class="description-container work-details-sections__description"
              v-html="column.content"
            />
          </div>
        </div>
      </template>
    </CustomHalfClipped>
    <ImageModal
      :showImageModal="showImageModal"
      :setShowImageModal="setShowImageModal"
      :imageUrl="targetImageUrl"
      :setImageIndex="setImageIndex"
    />
  </div>
</template>

<script>
import { Carousel3d, Slide } from "vue-carousel-3d";
import isDeviceSmart from "../../../helpers/DetectIsDeviceSmart";
import redirectToNewTab from "../../../helpers/RedirectToNewTab";
import CustomHalfClipped from "../../shared/CustomHalfClipped";
import ImageModal from "../../shared/ImageModal";
import * as WORK_COLUMNS_TYPES from "../../../constants/ColumnsContentTypes";

export default {
  props: ["section"],
  data() {
    return {
      showImageModal: false,
      targetImageIndeces: {
        rowId: null,
        imageIndex: null
      },
      targetImageUrl: ""
    };
  },
  components: {
    Carousel3d,
    Slide,
    CustomHalfClipped,
    ImageModal
  },
  computed: {
    WORK_COLUMNS_TYPES() {
      return WORK_COLUMNS_TYPES;
    }
  },
  methods: {
    toggleFullScreenMode(e, video) {
      if (video.is_auto_play) {
        e.preventDefault();
        let elem = document.getElementById(`video${video.id}`);

        if (!document.fullscreenElement) {
          elem.requestFullscreen().catch(err => {
            alert(
              `Error attempting to enable full-screen mode: ${err.message} (${err.name})`
            );
          });
        } else {
          document.exitFullscreen();
        }
      }
    },
    getImages(rowId) {
      return this.section.rows[rowId].columns.filter(
        col => col.type === WORK_COLUMNS_TYPES.IMAGE
      );
    },
    getWidth(ratio) {
      return isDeviceSmart() ? "100%" : ratio + "%";
    },
    setShowImageModal(flag) {
      this.showImageModal = flag;
    },
    openImageModal(rowId, imageIndex) {
      this.targetImageIndeces = {
        ...this.targetImageIndeces,
        rowId,
        imageIndex
      };
      this.targetImageUrl = this.getImages(rowId)[
        imageIndex
      ].img_content[0].url;
      this.setShowImageModal(true);
    },
    setImageIndex(dir) {
      const { rowId, imageIndex } = this.targetImageIndeces;
      const imagesLength = this.getImages(rowId).length;

      if (dir === "next")
        this.targetImageIndeces = {
          ...this.targetImageIndeces,
          imageIndex: imageIndex === imagesLength - 1 ? 0 : imageIndex + 1
        };
      else
        this.targetImageIndeces.imageIndex =
          imageIndex === 0 ? imagesLength - 1 : imageIndex - 1;

      this.targetImageUrl = this.getImages(rowId)[
        this.targetImageIndeces.imageIndex
      ].img_content[0].url;
    }
  },
  updated() {
    redirectToNewTab("description-container");
  }
};
</script>

<style lang="scss" scoped>
@import "../../../assets/sass/website/shared/work-service-sections/work-details-section.scss";
</style>
